import request from "../utils/request.js";

/**
 * 
 * @param params
 * @returns {AxiosPromise}
 */
export function categories(params) {
  return request({
    url: "/categories",
    method: "get",
    params: params
  });
}

/**
 * 
 * @param params
 * @returns {AxiosPromise}
 */
export function destroy(params) {
  return request({
    url: "/category/delete",
    method: "get",
    params: params
  });
}

/**
 * 
 * @param params
 * @returns {AxiosPromise}
 */
export function create(params) {
  return request({
    url: "/category/create",
    method: "post",
    data: params
  });
}
/**
 * 
 * @param params
 * @returns {AxiosPromise}
 */
export function UpdateField(params) {
  return request({
    url: "/category/update_field",
    method: "get",
    params: params
  });
}
