import React, { Fragment } from "react";
import {
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Tooltip,
  IconButton,
  Table,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
  Dialog
} from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import EditIcon from "@material-ui/icons/Edit";
import RotateLeftIcon from "@material-ui/icons/RotateLeft";
import FormatClearIcon from "@material-ui/icons/FormatClear";
import RestoreFromTrash from "@material-ui/icons/RestoreFromTrash";
import LockIcon from "@material-ui/icons/Lock";
import LockOpenIcon from "@material-ui/icons/LockOpen";
import SaveAlt from "@material-ui/icons/SaveAlt";
import Computer from "@material-ui/icons/Computer";
import Announcement from "@material-ui/icons/Announcement";
import LinkIcon from "@material-ui/icons/Link";
import { withStyles } from "@material-ui/core/styles";
import HelperTooltips from "../../components/helperTooltips";
import RepositoryStatus from "./repositoryStatus";
import RepositoryJobStatus from "./repositoryJobStatus";
import Snackbar from "../../components/snackbar";
import {
  deleteDepend,
  destroy,
  gitPull,
  UpdateField,
  pruneBranch,
  discardChange
} from "../../api/repository";
import EditField from "../../components/editField";
import { tooltip } from "../../assets/jss/common";

const styles = () => ({
  table: {
    minWidth: 400
  },
  tooltip
});

class RepositoryTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      destroyDialogShow: false,
      editField: {
        id: 0,
        open: false,
        rows: 1,
        desc: "",
        field: "",
        fieldContent: ""
      }
    };
    this.destroyId = 0; //id
  }

  destroyDialogOpen(id) {
    this.destroyId = id;
    this.setState({ destroyDialogShow: true });
  }

  destroyDialogClose() {
    this.setState({ destroyDialogShow: false });
  }

  deleteRepositoryDepend(row) {
    if (row.Status !== 1) {
      Snackbar.warning("");
      return;
    }
    deleteDepend({ id: row.ID })
      .then(r => {
        Snackbar.success(r);
      })
      .catch(() => {});
  }
  discardRepoChange(row) {
    discardChange({ id: row.ID })
      .then(() => {
        Snackbar.success(" git checkout . ");
      })
      .catch(() => {});
  }
  destroyConfirm() {
    destroy({ id: this.destroyId })
      .then(r => {
        this.setState({ destroyDialogShow: false });
        this.props.refresh();
      })
      .catch(() => {});
  }

  repositoryGitPull(row) {
    gitPull({ id: row.ID })
      .then(() => {
        Snackbar.success("");
      })
      .catch(() => {});
  }

  repositoryPruneBranch(row) {
    pruneBranch({ id: row.ID })
      .then(() => {
        Snackbar.success("");
      })
      .catch(() => {});
  }

  clickEditField(row, inputRows, desc, field) {
    this.setState({
      editField: {
        id: row.ID,
        open: true,
        rows: inputRows,
        desc: desc,
        field: field,
        fieldContent: row[field]
      }
    });
  }
  editFieldSuccess(id, field, fieldContent) {
    UpdateField({ id, field, fieldContent })
      .then(() => {
        this.closeResetEditFieldDialog();
        this.props.refresh();
      })
      .catch(() => {});
  }
  closeResetEditFieldDialog() {
    this.setState({
      editField: {
        id: 0,
        open: false,
        rows: 1,
        desc: "",
        field: "",
        fieldContent: ""
      }
    });
  }

  render() {
    const { classes } = this.props;
    return (
      <Fragment>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center">
                
                <HelperTooltips help="" />
              </TableCell>
              <TableCell align="center">
                
                <HelperTooltips help="" />
              </TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center">
                
                <HelperTooltips help="" />
              </TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center">
                
                <HelperTooltips help="/yarn.lock" />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {this.props.tableData.map(row => (
              <TableRow key={row.ID} hover>
                <TableCell component="th" scope="row">
                  {row.ID}
                </TableCell>
                <TableCell align="center">
                  <Tooltip title={row.Url} interactive>
                    <IconButton color="primary">
                      <LinkIcon />
                    </IconButton>
                  </Tooltip>
                </TableCell>
                <TableCell align="left">
                  <EditIcon
                    style={{
                      fontSize: 14,
                      marginRight: 10,
                      cursor: "pointer"
                    }}
                    onClick={this.clickEditField.bind(
                      this,
                      row,
                      1,
                      "",
                      "Name"
                    )}
                    color="primary"
                  />
                  {row.Name}
                </TableCell>
                <TableCell align="center">
                  <RepositoryStatus status={row.Status} />
                </TableCell>
                <TableCell align="center">
                  <RepositoryJobStatus status={row.JobStatus} />
                </TableCell>
                <TableCell align="center">
                  {row.UserName && row.Password ? (
                    <Tooltip
                      title={<div>{row.UserName}</div>}
                      interactive
                    >
                      <IconButton color="primary">
                        <LockIcon />
                      </IconButton>
                    </Tooltip>
                  ) : (
                    <Tooltip title="" interactive>
                      <IconButton color="primary">
                        <LockOpenIcon />
                      </IconButton>
                    </Tooltip>
                  )}
                </TableCell>
                <TableCell align="center">
                  <Tooltip
                    title={row.TerminalInfo}
                    classes={{ tooltip: classes.tooltip }}
                    interactive
                  >
                    <IconButton color="primary">
                      <Computer />
                    </IconButton>
                  </Tooltip>
                </TableCell>
                <TableCell align="center">
                  <Tooltip
                    title={row.Desc}
                    classes={{ tooltip: classes.tooltip }}
                    interactive
                  >
                    <IconButton
                      color="primary"
                      onClick={this.clickEditField.bind(
                        this,
                        row,
                        8,
                        "",
                        "Desc"
                      )}
                    >
                      <Announcement />
                    </IconButton>
                  </Tooltip>
                </TableCell>
                <TableCell align="center">
                  <span className="tag">{row.DependTools.toUpperCase()}</span>
                </TableCell>
                <TableCell align="center">
                  <Tooltip title="" interactive>
                    <IconButton
                      color="primary"
                      onClick={this.repositoryGitPull.bind(this, row)}
                    >
                      <SaveAlt />
                    </IconButton>
                  </Tooltip>
                  <Tooltip title="" interactive>
                    <IconButton
                      color="primary"
                      onClick={this.repositoryPruneBranch.bind(this, row)}
                    >
                      <FormatClearIcon />
                    </IconButton>
                  </Tooltip>
                  <Tooltip title="" interactive>
                    <IconButton
                      color="primary"
                      onClick={this.discardRepoChange.bind(this, row)}
                    >
                      <RotateLeftIcon />
                    </IconButton>
                  </Tooltip>
                  <Tooltip title="" interactive>
                    <IconButton
                      color="primary"
                      onClick={this.deleteRepositoryDepend.bind(this, row)}
                    >
                      <RestoreFromTrash />
                    </IconButton>
                  </Tooltip>
                  <Tooltip title="" interactive>
                    <IconButton
                      color="primary"
                      onClick={this.destroyDialogOpen.bind(this, row.ID)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </Tooltip>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        <EditField
          id={this.state.editField.id}
          open={this.state.editField.open}
          desc={this.state.editField.desc}
          rows={this.state.editField.rows}
          field={this.state.editField.field}
          fieldContent={this.state.editField.fieldContent}
          onClose={this.closeResetEditFieldDialog.bind(this)}
          editSuccess={this.editFieldSuccess.bind(this)}
        />
        <Dialog
          open={this.state.destroyDialogShow}
          onClose={this.destroyDialogClose.bind(this)}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{"?"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={this.destroyDialogClose.bind(this)}
              color="primary"
            >
              
            </Button>
            <Button
              onClick={this.destroyConfirm.bind(this)}
              color="secondary"
              autoFocus
            >
              
            </Button>
          </DialogActions>
        </Dialog>
      </Fragment>
    );
  }
}

export default withStyles(styles)(RepositoryTable);
