import request from "../utils/request.js";

/**
 * 
 * @param params
 * @returns {AxiosPromise}
 */
export function jobs(params) {
  return request({
    url: "/jobs",
    method: "get",
    params: params
  });
}

/**
 * ID
 * @param params
 * @returns {AxiosPromise}
 */
export function jobsFind(params) {
  return request({
    url: "/jobs_find",
    method: "get",
    params: params
  });
}

/**
 * 
 * @param params
 * @returns {AxiosPromise}
 */
export function create(params) {
  return request({
    url: "/job/create",
    method: "post",
    data: params
  });
}

